import { CourseState } from "../../states/CourseState"
import { ProgressionState } from "../../states/ProgressionState"
import React from "react"
import { UserState } from "../../states/UserState"
import { ISerializableEntity, TermtanglesSerialized } from "../../interfaces/SerializableInterfaces"
import { DateTime } from "luxon"
import FileSaver from "file-saver"

export class SerializableVersionNumber implements ISerializableEntity {
  private _versionNumber: number = 1;

  serialize(): Object {
    return this._versionNumber;
  }

  async deserialize(imported: TermtanglesSerialized): Promise<void> {
    if (!imported._version) {
      throw "ERROR: Version not included.";
    }

    console.log("Termtangle data version: " + imported._version);
  }
}

export class LocalImportExportViewModel {
  private _serializables = new Map<string, ISerializableEntity>();

  constructor (userState: UserState, progressionState: ProgressionState, courseState: CourseState) {
    this._serializables.set("_version", new SerializableVersionNumber());
    this._serializables.set("userState", userState);
    this._serializables.set("progressionState", progressionState);
    this._serializables.set("courseState", courseState);
  }

  serializeToFile(data: TermtanglesSerialized) {
    FileSaver.saveAs(
      new Blob([JSON.stringify(data)], { type: "application/json;charset=utf-8" }),
      `termtangles_${DateTime.now().toFormat("yyyyMMdd_HHmmssSSS")}.json`,
    );
  }

  export() {
    const data: TermtanglesSerialized = {
      _version: 0,
      userState: {studyLevel: ""}
    };
    this._serializables.forEach((serializable, key) => {
      data[key] = serializable.serialize();
    });
    this.serializeToFile(data);
  }

  async deserializeFromString (data: string) {
    try {
      const json: TermtanglesSerialized = JSON.parse(data);

      return Promise.resolve(
        this._serializables.get("_version")?.deserialize(json).then(() => 
          this._serializables.get("userState")?.deserialize(json).then(() => 
            Promise.resolve([
              this._serializables.get("progressionState")?.deserialize(json),
              this._serializables.get("courseState")?.deserialize(json),
            ])
          )
        )
      ).catch((error) => { console.error(error) })
    } catch (e) {
      console.error(`ERROR: Invalid JSON (${e.message})`);
    }
  }

  import (event: React.ChangeEvent<HTMLInputElement>) {
    const file: Blob = event.target.files![0];
    
    let reader = new FileReader();
    reader.onload = async (ev) => {
      if (ev.target?.result) {
        await this.deserializeFromString(ev.target.result.toString());
        event.target.value = "";
      }
    }
    reader.readAsText(file);
  }
}
