export interface ICurriculumContainer {
    id: string;
    title: string;
    description: string;
    creditPoints: number;
    elements: ICurriculumElement[];
    containers: ICurriculumContainer[];
    order: number;
}

export interface ICurriculumElement {
    name: string;
    description: string;
    type: string;
    creditPoints: number;
    itemCode: string;
    url: string;
    order: number;
    isCourse: () => boolean;
    isSpecialisation: () => boolean;
}

export interface IOrConditionalTree {
    or: ConditionalTree[]
}
export interface IAndConditionalTree {
    and: ConditionalTree[]
}

export const isOrConditionalTree = (obj: any): obj is IOrConditionalTree => {
    return obj && obj.or
}

export const isAndConditionalTree = (obj: any): obj is IAndConditionalTree => {
    return obj && obj.and
}

export type ConditionalTree = IOrConditionalTree | IAndConditionalTree | string

export interface ICourse {
    id: string;
    code: string;
    contentTypeLabel: string;
    corequisites: string[];
    creditPoints: number;
    description: string;
    effectiveDate: Date; // Form (new Date('YYYY-MM-DD'));
    enrolmentRules: string[];
    exclusion: string[];
    facultyName: string;
    implementationYear: number;
    keywords: string[];
    prerequisites: ConditionalTree;
    schoolName: string;
    studyLevel: string;
    termOffering: string[];
    title: string;
    urlMap: string;
}

export interface IProgram {
    id: string;
    code: string;
    title: string;
    description: string;
    academicItemType: string;
    creditPoints: number;
    curriculumStructure: Array<ICurriculumContainer>;
    facultyName: string;
    schoolName: string;
    additionalInfo: string;
    implementationYear: number;
    intakePeriod: Array<string>;
    programConstraints: Array<any>;
    studyLevel: string;
}

export interface ISpecialisation {
    id: string;
    code: string;
    title: string;
    description: string;
    academicItemType: string;
    creditPoints: number;
    curriculumStructure: Array<ICurriculumContainer>;
    facultyName: string;
    schoolName: string;
    implementationYear: number;
    keywords: string[];
    studyLevel: string;
    urlMap: string;
}

export interface IProgressionEntity {
    id: string;
    code: string;
    title: string;
    description: string;
    academicItemType: string;
    creditPoints: number;
    structureSummary: string,
    curriculumStructure: Array<ICurriculumContainer>;
    facultyName: string;
    schoolName: string;
    progressionStatus: ProgressionStatus,
    setPlanned: () => void,
    setUnplanned: () => void,
}

export enum ProgressionStatus {
    UNPLANNED,
    PLANNED,
}

export interface IValidatableEntity {
    addError: (error: IError) => void
    removeError: (error: IError) => void
    hasErrors: () => boolean
    clearErrors: () => void
    errors: IError[]
}

export enum ErrorCode {
    UNKNOWN,
    COURSE_NOT_OFFERED_IN_SELECTED_TERM,
    PREREQUISITES_NOT_PLANNED,  
    PREREQUISITE_REQUIREMENT_NOT_MET,
    COREQUISITE_REQUIREMENT_NOT_MET,
    EXCLUSION_COURSE_CONFLICT,
}

export interface IError {
    code: ErrorCode,
    message: string,
    missing: string,
}

export enum StudyLevel {
    UNDERGRADUATE="undergraduate",
    POSTGRADUATE="postgraduate",
    RESEARCH="research",
}
