import { Observer, observer } from 'mobx-react'
import { Accordion, Menu } from 'semantic-ui-react'
import { ResetButtonStyle } from '../../../config/ResetButtonStyle'
import { FilterModel } from '../../../models/Filters/FilterModel'
import { FilterSection } from './FilterSection'
import './ResetButton.css'
export interface IFilterMenuProps<T> {
  searchResults: T[]
  filters: FilterModel<T>[]
}

export const FilterMenuView = observer(<T,>(props: IFilterMenuProps<T>) => {
  const handlereset = () => {
    props.filters.forEach((filter) => {
      filter.getFilterState(props.searchResults).forEach((element) => {
        filter.resetValue(element.filterElementName)
      })
    })
  }

  return (
    <Observer>
      {
        () => {
          return <div >
            <div className="resetbutton">
              <button style={ResetButtonStyle} onClick={() => handlereset()}>Reset Filter</button>
            </div>
            <Accordion fluid as={Menu} vertical>
              {
                props.filters.map(filter => {
                  return <FilterSection
                    searchResults={props.searchResults}
                    filter={filter}
                  />
                })
              }
            </Accordion>

          </div>
        }

      }
    </Observer>
  )
})
