import parse from 'html-react-parser';
import { Observer, observer } from "mobx-react";
import React from "react";
import sanitizeHtml from 'sanitize-html';
import { Accordion, Icon, Label } from "semantic-ui-react";
import styled from 'styled-components';
import { CurriculumContainer } from "../../../models/CurriculumModel";
import { ProgressionPlannerViewModel } from "../ProgressionPlannerViewModel";
import { CurriculumElementsView } from "./CurriculumElementsView";

export interface ICurriculumContainerViewProps {
  entityId: string
  curriculumContainer: CurriculumContainer[]
  progressionPlannerViewModel: ProgressionPlannerViewModel
}

export const CurriculumAccordionCreditPoints = styled(Label)`
  &&& {
    margin-left:auto
    
  }
`

export const CurriculumAccordionTitle = styled(Accordion.Title)`
  &&& {
    display:flex;
  }
`
export const CurriculumDescriptionDiv = styled.div`
  display: flex;
  justify-content: space-between;
`

export const CurriculumExpandCollapseLink = styled.a`
  min-width: 80px;
  text-align: right;
`

export const CurriculumContent = styled.div`
  margin-left:10px;
`

export const CurriculumContainerView = observer((props: ICurriculumContainerViewProps) => {
  const getCreditPointsView = (container: CurriculumContainer) => {
    const uocOfChildren = props.progressionPlannerViewModel.calculateUocOfChildren(container)
    if (container.creditPoints > 0) {
      return <CurriculumAccordionCreditPoints
        size="small"
        color={uocOfChildren === container.creditPoints
          ? "green"
          : uocOfChildren < container.creditPoints
            ? "orange"
            : "yellow"
        }
      >
        {uocOfChildren} / {container.creditPoints} UOC
      </CurriculumAccordionCreditPoints>
    } else if (uocOfChildren > 0) {
      return <CurriculumAccordionCreditPoints
        size="small"
        color="green"
      >
        {uocOfChildren} UOC
      </CurriculumAccordionCreditPoints>
    }
  }

  return <Observer>
    {
      () => {
        return props.curriculumContainer.length > 0
          ? <Accordion.Accordion
            key={props.entityId}
            exclusive={false}
            panels={[...props.curriculumContainer]
              .sort((a, b) => Number(a.order) - Number(b.order))
              .map(container => {
                return {
                  key: container.id,
                  title: <CurriculumAccordionTitle>
                    <Icon className={
                      props.progressionPlannerViewModel.isContainerActive(container.id)
                      ? "caret down"
                      : "caret right"
                    }/>
                    {container.title}
                    {getCreditPointsView(container)}
                  </CurriculumAccordionTitle>,
                  content: {
                    content: <div>
                      <CurriculumContent>
                      <CurriculumDescriptionDiv>
                        <span>{parse(sanitizeHtml(container.description))}</span>
                        {
                          container.containers.length > 0
                            ? <CurriculumExpandCollapseLink
                              href="#"
                              onClick={() => props.progressionPlannerViewModel.toggleAllContainers(container)}
                            >
                              {
                                props.progressionPlannerViewModel.isAnySubContainerActive(container)
                                  ? "Collapse All"
                                  : "Expand All"
                              }
                            </CurriculumExpandCollapseLink>
                            : <></>
                        }
                      </CurriculumDescriptionDiv>
                      <CurriculumElementsView entityId={props.entityId} curriculumElements={container.elements} />
                      </CurriculumContent>
                      <CurriculumContainerView
                        entityId={props.entityId}
                        curriculumContainer={container.containers}
                        progressionPlannerViewModel={props.progressionPlannerViewModel}
                      />
                    </div>
                  },
                  active: props.progressionPlannerViewModel.isContainerActive(container.id),
                  onTitleClick: () => { props.progressionPlannerViewModel.toggleContainer(container.id) }
                }
              })}
          />
          : <></>
      }
    }
  </Observer>
});
