import { ICurriculumContainer, ICurriculumElement } from "../interfaces/ModelInterfaces";
import {v4 as uuidv4} from 'uuid';

export class CurriculumContainer implements ICurriculumContainer {
    public readonly id: string = uuidv4();
    constructor(
        public readonly title: string,
        public readonly description: string,
        public readonly creditPoints: number,
        public readonly elements: ICurriculumElement[],
        public readonly containers: ICurriculumContainer[],
        public readonly order: number,
    ) {
    }

    toString() {
        return this.title + ', ' + this.description;
    }
}

export class CurriculumElement implements ICurriculumElement {
    constructor(
        public readonly name: string,
        public readonly description: string,
        public readonly type: string,
        public readonly creditPoints: number,
        public readonly itemCode: string,
        public readonly url: string,
        public readonly order: number,
    ) { }

    public isCourse(): boolean {
        return this.type.toLowerCase() === "course"
    }

    public isSpecialisation(): boolean {
        return ["major", "minor", "specialisation", "honours"].includes(this.type.toLowerCase())
    }

    toString() {
        return this.name + ', ' + this.type;
    }
}

// Arg: Array of json data where each json contains data for Children Obj
export const parseCurriculumContainers = (childrenData: Array<any>): Array<CurriculumContainer> => {
    let rootContainerList: CurriculumContainer[] = [];
    
    childrenData.forEach((data) => {
        let childContainerList: CurriculumContainer[] = [];
        let elementList: CurriculumElement[] = [];
        // If nested children containers exist, recursively transform jsonData 
        // into Children obj
        if (data.children.length > 0) {
            childContainerList = parseCurriculumContainers(data.children);
        }

        if (data.relationships.length > 0) {
            elementList = parseCurriculumElements(data.relationships);
        }

        let container = new CurriculumContainer(
            data.title,
            data.description,
            Number(data.creditPoints),
            elementList,
            childContainerList,
            Number(data.order)
        );

        rootContainerList.push(container);
    })

    return rootContainerList;
}

// Arg: Array of json data where each json contains data for Relationship obj
export const parseCurriculumElements = (relationData: Array<any>): Array<CurriculumElement> => {
    let relationshipList: CurriculumElement[] = [];

    relationData.forEach((data) => {
        let relationshipObj = new CurriculumElement(
            data.name,
            data.description,
            data.type,
            Number(data.creditPoints),
            data.itemCode,
            data.url,
            Number(data.order)
        )

        relationshipList.push(relationshipObj);
    })

    return relationshipList;
}
