import { createContext } from 'react'
import { CourseState } from '../states/CourseState'
import { ProgressionState } from '../states/ProgressionState';
import { SearchDataState } from '../states/SearchDataState';
import { UserState } from '../states/UserState';

const userState = new UserState();
const courseState = new CourseState(userState);
const progressionState = new ProgressionState(userState);
const searchDataState = new SearchDataState(courseState, progressionState);

export const ApplicationContext = createContext({
    userState,
    courseState,
    progressionState,
    searchDataState,
})
