import { makeObservable, observable } from "mobx";
import { ProgressionStatus } from "../../interfaces/ModelInterfaces";
import { CourseStatus } from "../../models/CourseModel";
import { CurriculumContainer } from "../../models/CurriculumModel";
import { ProgramModel } from "../../models/ProgramModel";
import { SpecialisationModel } from "../../models/SpecialisationModel";
import { CourseState } from "../../states/CourseState";
import { ProgressionState } from "../../states/ProgressionState";

export interface IProgressionPlannerState {
  id: string,
  title: string,
  programs: ProgramModel[],
  specialisations: SpecialisationModel[],
}

export class ProgressionPlannerViewModel {
  private _progressionState: ProgressionState
  private _courseState: CourseState
  private _progressionPlannerState: IProgressionPlannerState
  private _displayableContainerPanels: Set<string> = new Set<string>()

  constructor(
    progressionState: ProgressionState,
    courseState: CourseState,
  ) {
    makeObservable<ProgressionPlannerViewModel,
      "_displayableContainerPanels"
    >(this, {
      _displayableContainerPanels: observable,
    })

    this._progressionState = progressionState
    this._progressionPlannerState = {
      id: "progressionPlanner",
      title: "Progression Planner",
      programs: [],
      specialisations: [],
    }
    this._courseState = courseState
  }

  get progressionState() {
    return this._progressionState;
  }

  get progressionPlannerState() {
    return this._progressionPlannerState;
  }

  calculateUocOfChildren(curriculumContainer: CurriculumContainer): number {
    return [...this.getUniqueUocOfCurriculumContainer(curriculumContainer).values()].reduce((a, b) => a + b, 0)
  }

  getUniqueUocOfCurriculumContainer(curriculumContainer: CurriculumContainer): Map<string, number> {
    let allChildren = new Map<string, number>()
    curriculumContainer.elements.forEach(element => {
      if (element.isCourse()) {
        let course = this._courseState.getCourseById(element.itemCode)
        if (course && course.courseStatus !== CourseStatus.UNPLANNED) {
          allChildren.set(element.itemCode, element.creditPoints)
        }
      } else if (element.isSpecialisation()) {
        let specialisation = this.progressionState.getSpecialisationById(element.itemCode)
        if (specialisation && specialisation.progressionStatus !== ProgressionStatus.UNPLANNED) {
          allChildren.set(element.itemCode, element.creditPoints)
        }
      }
    })
    curriculumContainer.containers.forEach(container => {
      let childResult = this.getUniqueUocOfCurriculumContainer(container)
      childResult.forEach((value, key) => allChildren.set(key, value))
    })
    return allChildren
  }

  toggleContainer(containerId: string) {
    if (this._displayableContainerPanels.has(containerId)) {
      this._displayableContainerPanels.delete(containerId)
    } else {
      this._displayableContainerPanels.add(containerId)
    }
  }

  isAnySubContainerActive(rootContainer: CurriculumContainer): boolean {
    return rootContainer.containers.filter(container => {
      return this._displayableContainerPanels.has(container.id)
        || this.isAnySubContainerActive(container)
    }).length > 0
  }

  toggleAllContainers(rootContainer: CurriculumContainer) {
    if (this.isAnySubContainerActive(rootContainer)) {
      rootContainer.containers.forEach(container => {
        this.setAllContainersInactive(container)
      })
    } else {
      rootContainer.containers.forEach(container => {
        this.setAllContainersActive(container)
      })
    }
  }

  private setAllContainersActive(rootContainer: CurriculumContainer) {
    rootContainer.containers.forEach(container => {
      this.setAllContainersActive(container)
    })
    this._displayableContainerPanels.add(rootContainer.id)
  }

  private setAllContainersInactive(rootContainer: CurriculumContainer) {
    rootContainer.containers.forEach(container => {
      this.setAllContainersInactive(container)
    })
    this._displayableContainerPanels.delete(rootContainer.id)
  }

  isContainerActive(containerId: string): boolean {
    return this._displayableContainerPanels.has(containerId)
  }
}
