import React from 'react';
import { LocalImportExportViewModel } from "./LocalImportExportViewModel"
import styled, { css } from "styled-components"

const ImportExportButton = styled.button`
  background: transparent;
  border-radius: 4px;
  border: 2px solid darkgrey;
  color: black;
  margin: 4px 2px;
  padding: 15px 32px;
  font-size: 20px;
  display: inline-block;
  width: 100%;
  height: 100%;
  cursor: pointer;

  transition-duration: 0.2s;
  :hover {
    background: #f2f2f2;
  }

  ${props => props.primary && css`
    background: darkgrey;

    :hover {
      background: lightgrey;
    }
  `}
`;

const ImportExportButtonContainer = styled.div`
  width: 48%;
  display: inline;
  background: transparent;
  float: left;
  margin: 0% 1%;
`;

const LocalImportExport = ({ localImportExportViewModel }: { [key: string]: LocalImportExportViewModel }) => {
  const inputRef = React.useRef<HTMLInputElement>(null);

  return (
    <React.Fragment>
      <ImportExportButtonContainer>
        <ImportExportButton primary onClick={() => { localImportExportViewModel.export() }}>
          Export
        </ImportExportButton>
      </ImportExportButtonContainer>
      <ImportExportButtonContainer>
        <ImportExportButton onClick={() => { inputRef.current?.click() }}>
          Import
        </ImportExportButton>
        <input
          ref={inputRef}
          onChange={(event) => localImportExportViewModel.import(event)}
          type="file"
          style={{ display: "none" }}
          accept="application/JSON"
        />
      </ImportExportButtonContainer>
    </React.Fragment >
  )
};

export default LocalImportExport;