import { CourseState } from "../../states/CourseState";
import { CourseModel } from "../../models/CourseModel";
import { TermModel } from "../../models/TermModel";

export class AutoPlannerViewModel {
    private courseState: CourseState;
  
    constructor(courseState: CourseState) {
        this.courseState = courseState;
    }

    get autoPlanCourseState(): CourseState {
        return this.courseState;
    }

    autoPlan() {
        let canAllocateCourses: boolean = true;
        let listYearsToTryAllocate: number[] = this.getYearsToTryAllocate(this.courseState.getMaxAllocatedYear);
        let listTermsToTryAllocate: string[] = ["Term 1", "Term 2", "Term 3"]

        while (canAllocateCourses === true) {
            let coursesToBeAllocated: CourseModel[] = this.courseState.plannedCourses;
            let nCoursesToAllocate: number = coursesToBeAllocated.length;
            let hasChanged = false;

            // Try for each year, term; if allocation spot is free & pre-req is met, add course to term
            for (var counter = 0; counter < coursesToBeAllocated.length; counter++) {
                let termAllocatedFlag = false;
                for (let i = 0; i < listYearsToTryAllocate.length; i++) {
                    if (termAllocatedFlag === false) {
                        for (let j = 0; j < listTermsToTryAllocate.length; j++) {
                            let canAssignToTerm : string[] | boolean = this.courseState.getMissingRequirementsWhenAssigningCourseToTerm(coursesToBeAllocated[counter], new TermModel(listYearsToTryAllocate[i], listTermsToTryAllocate[j]));
                            let termHasSpaceToAllocate : boolean = this.termHasLessThanThreeCourses(listYearsToTryAllocate[i], listTermsToTryAllocate[j], this.courseState.termCourseMap);
                            let termIsDisabled: boolean = new TermModel(listYearsToTryAllocate[i], listTermsToTryAllocate[j]).isBefore(new TermModel(2021, "Term 2"))
                            if (canAssignToTerm.length === 0) {
                                canAssignToTerm = true;
                            } else {
                                canAssignToTerm = false;
                            }

                            if (!termIsDisabled && termHasSpaceToAllocate && canAssignToTerm) {
                                this.courseState.assignCourseToTerm(coursesToBeAllocated[counter], new TermModel(listYearsToTryAllocate[i], listTermsToTryAllocate[j]));
                                termAllocatedFlag = true;
                                hasChanged = true;
                                break;
                            }
                        }
                    }
                }
            }

            // Terminating condition
            // If there are no changes, this means that there are no more courses that can be allocated
            // and hence terminate allocation attempt
            if (hasChanged === false) {
                canAllocateCourses = false;
            }
        }
    }

    termHasLessThanThreeCourses(year: number, term: string, allocatedCourses: Map<string, CourseModel[]>) : boolean {
        let dictKey = year + "_" + term;

        if (allocatedCourses.has(dictKey)) {
            // A term already has 3 courses, can't allocate
            if (allocatedCourses.get(dictKey)!.length >= 3) {
                return false;
            }

            // Term exists in allocatedTerm array, but has less than 3 courses, can allocate
            return true;
        } else {
            // Term has not been allocated, can allocate
            return true;
        }
    }

    getYearsToTryAllocate(maxYear: number) {
        var list: number[] = [];

        // Return list of years with excess, incase all previous terms are full
        for (var i = 2021; i <= maxYear + 4; i++) {
            list.push(i);
        }

        return list;
    }
}