import { Observer, observer } from 'mobx-react';
import React from 'react';
import { Accordion } from 'semantic-ui-react';
import styled from 'styled-components';
import { IProgressionEntity } from '../../interfaces/ModelInterfaces';
import { CurriculumContainerView } from './CurriculumView/CurriculumContainerView';
import { ProgressionPlannerViewModel } from './ProgressionPlannerViewModel';
import parse from 'html-react-parser';
import sanitizeHtml from 'sanitize-html';

export interface IProgressionEntityViewProps {
  progressionPlannerViewModel: ProgressionPlannerViewModel
  entity: IProgressionEntity
}

const ProgressionEntityTitle = styled.a`
color: black;
background-color: transparent;
text-decoration: none;

&:hover {
  color: #db2828;
  background-color: transparent;
  text-decoration: line-through;
}
`

const ProgressionEntityView = observer((props: IProgressionEntityViewProps) => {
  return <Observer>
    {() => {
      return <>
        <h2>
          <ProgressionEntityTitle
            href="#"
            onClick={() => props.entity.setUnplanned()}
          >{props.entity.title}</ProgressionEntityTitle>
        </h2>
        <span>{parse(sanitizeHtml(props.entity.structureSummary))}</span>
        <Accordion fluid styled>
          <CurriculumContainerView
            entityId={props.entity.id}
            curriculumContainer={props.entity.curriculumStructure}
            progressionPlannerViewModel={props.progressionPlannerViewModel}
          />
        </Accordion>
      </>
    }}
  </Observer>
})

export default ProgressionEntityView;
