import { Observer, observer } from "mobx-react"
import React, { useState } from "react"
import { Button, Icon, Item } from "semantic-ui-react"
import { ProgressionStatus } from "../../../interfaces/ModelInterfaces"
import { ProgramModel } from "../../../models/ProgramModel"
import { ResultItem, ResultItemContent, ResultItemExtra } from "../SearchPanelStyles"

export interface IResultViewProps<T> {
  program: T
}

export const ProgramResultView = observer((props: IResultViewProps<ProgramModel>) => {
  const [showOptions, setShowOptions] = useState(false)

  const openInNewTab = (url) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
  }

  return <Observer>
    {
      () => {
        return (
          <ResultItem
            onMouseEnter={() => setShowOptions(true)}
            onMouseLeave={() => setShowOptions(false)}
          >
            <ResultItemContent>
              <Item.Header>{props.program.title}</Item.Header>
              <Item.Description>{props.program.academicItemType} - {props.program.code}</Item.Description>
            </ResultItemContent>
            {
              !showOptions
                ?
                <ResultItemContent>
                  <ResultItemExtra>{props.program.creditPoints} UOC</ResultItemExtra>
                  <ResultItemExtra>
                    {
                      props.program.progressionStatus === ProgressionStatus.PLANNED
                        ? <Icon color="green" className="calendar alternate outline" />
                        : <></>
                    }
                  </ResultItemExtra>
                </ResultItemContent>
                :
                <ResultItemContent>
                  <ResultItemExtra>
                    <Button.Group>
                      <Button
                        basic = {props.program.progressionStatus === ProgressionStatus.UNPLANNED}
                        icon={props.program.progressionStatus === ProgressionStatus.UNPLANNED ? "plus circle" : "minus circle"}
                        content={props.program.progressionStatus === ProgressionStatus.UNPLANNED ? "Add" : "Remove"}
                        color={props.program.progressionStatus === ProgressionStatus.UNPLANNED ? "green" : "red"}
                        size="medium"
                        onClick= {() => {
                          if (props.program.progressionStatus === ProgressionStatus.UNPLANNED) {
                            props.program.setPlanned()
                          } else {
                            props.program.setUnplanned()
                          }
                        }}
                      />
                      <Button
                        basic = {true}
                        icon={'book'}
                        content={'Handbook'}
                        color={'red'}
                        size="medium"
                        onClick= {() => openInNewTab(`https://www.handbook.unsw.edu.au${props.program.urlMap}`) }
                      />
                    </Button.Group>
                  </ResultItemExtra>
                </ResultItemContent>
            }
          </ResultItem>
        )
      }
    }
  </Observer>
})
