import styled from 'styled-components'
import { CourseModel } from '../../../models/CourseModel';
import { Draggable } from 'react-beautiful-dnd'
import Tooltip from '@material-ui/core/Tooltip';
import { IError } from '../../../interfaces/ModelInterfaces';
import { TermModel } from '../../../models/TermModel';

const Container = styled.div`
    border: 1px solid lightgrey;
    border-radius:8px;
    padding: 8px;
    margin: 0px 2px 8px 2px;
    background-color: ${props => (
        props.isPrerequisiteOfCurrentDraggable
        ? '#c5eafc'
        : props.hasErrors()
            ? '#f5bfc8'
            : props.isDragging
                ? 'lightgrey'
                : 'white'
        )
    };
    ${props => props.horizontal ? "" : "flex-grow: 1;"}
    height: fit-content;
    max-width: 350px;
`

const tooltipStyle = {
    fontSize: 13,
}

interface ICourseViewProps {
    droppableIndex: number
    course: CourseModel
    prerequisitesOfCurrentDraggable: Set<string>
    horizontal?: boolean
}

const CourseView = ({ droppableIndex, course, prerequisitesOfCurrentDraggable, horizontal }: ICourseViewProps) => {
    let termOffering = formatTermOffering(course.termOffering, course.allocatedTerm);
    let errorList: string[] = formatErrors(course.errors);

    return (
            <Draggable draggableId={course.code} index={droppableIndex}>
                {((provided, snapshot) => (
                    <Container
                        horizontal={horizontal}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        ref={provided.innerRef}
                        isDragging={snapshot.isDragging}
                        isPrerequisiteOfCurrentDraggable={prerequisitesOfCurrentDraggable.has(course.id)}
                        hasErrors={() => course.hasErrors()}
                    >
                        <Tooltip title={
                            <span>
                                <p style={tooltipStyle}>{termOffering}</p>
                                <p style={tooltipStyle}>{errorList[0]}</p>
                                <p style={tooltipStyle}>{errorList[1]}</p>
                            </span>} 
                            aria-label="add" aria-multiline="true" arrow={true}
                        >
                            <p> {course.code} - {course.title}</p>     
                        </Tooltip>
                    </Container>
                ))}
            </Draggable>
    )
}

function formatErrors(errors: IError[]): string[] {
    let output: string[] = [];
    let missing: string[] = [];
    let missingFormatted: string[] = [];
    const regex = /<br\/>/ig;

    if (errors.length === 0) {
        return [""];
    }

    if (errors.length === 1) {
        return [errors[0].message.replace(regex, ""), `${errors[0].missing}`];
    }

    for (let i = 0; i < errors.length; i++) {
        output.push(errors[i].message.replace(regex, ""));
        missing.push(errors[i].missing);
    }

    // Format missing arr for empty elements and remove from array before joining
    for (let i = 0; i < errors.length; i++) {
        if (missing[i] !== "") {
            missingFormatted.push(missing[i]);
        }
    }

    return [output.join(" & "), missingFormatted.join(" & ")];
}

function formatTermOffering(termOffering: string[], allocatedPeriod: TermModel | undefined) {
    termOffering = termOffering.sort();
    let output: string = termOffering.join(", ");

    output = `Term Offerings: ${output}`;

    return output;
}

export default CourseView
