import { Observer, observer } from 'mobx-react';
import React from 'react';
import { Container } from 'semantic-ui-react';
import styled from 'styled-components';
import ProgressionEntityView from './ProgressionEntityView';
import { ProgressionPlannerViewModel } from './ProgressionPlannerViewModel';

export const ProgressionContainer = styled(Container)`
  margin:1em 0;
`

export interface IProgressionPlannerProps {
  progressionPlannerViewModel: ProgressionPlannerViewModel
}

const ProgresionPlanner = observer(({ progressionPlannerViewModel }: IProgressionPlannerProps) => {
  return <Observer>
    {() => {
      return (
        <React.Fragment>
          <ProgressionContainer>
            {
              progressionPlannerViewModel
                .progressionState
                .plannedProgressionEntities
                .map(entity => {
                  return <ProgressionEntityView
                    entity={entity}
                    progressionPlannerViewModel={progressionPlannerViewModel}
                  />
                })
            }
          </ProgressionContainer>
        </React.Fragment>
      )
    }}
  </Observer>
})

export default ProgresionPlanner;
