
import { observer, Observer } from "mobx-react"
import React, { useState } from "react"
import { Grid } from "semantic-ui-react"
import { FilterMenuView } from "../Filter/FilterMenuView"
import { SpecialisationResultCollectionView } from "../Results/SpecialisationResultCollectionView"
import { ResultsColumn, ResultsPagination } from "../SearchPanelStyles"
import { ISearchTabProps } from "./SearchTabInterfaces"

export const SpecialisationSearchTab = observer((props: ISearchTabProps) => {
  const [activePageNumber, setActivePageNumber] = useState(1)


  return <Observer>
    {
      () => {
        return (
          <>
            <Grid columns={12} divided container>
              <Grid.Row>
                <Grid.Column key="filter" width={4}>
                  <FilterMenuView
                    searchResults={props.searchPanelViewModel.searchResults.specialisationSearchResults}
                    filters={props.searchPanelViewModel.searchFilters.specialisationFilters}
                  />
                </Grid.Column>
                <ResultsColumn key="results" width={12}>
                  <SpecialisationResultCollectionView results={
                    props.searchPanelViewModel.searchResults.specialisationSearchResults
                      .slice(
                        props.searchPanelViewModel.resultsPerPage * (activePageNumber - 1),
                        Math.min(props.searchPanelViewModel.searchResults.specialisationSearchResults.length, props.searchPanelViewModel.resultsPerPage * activePageNumber),
                      )
                  } />
                  <ResultsPagination
                    totalPages={Math.ceil(props.searchPanelViewModel.searchResults.specialisationSearchResults.length / props.searchPanelViewModel.resultsPerPage)}
                    onPageChange={(e, data) => {
                      setActivePageNumber(Number(data.activePage))
                      // console.log(data)
                    }}
                    activePage={activePageNumber}
                  />
                </ResultsColumn>
              </Grid.Row>

            </Grid>
          </>
        )
      }
    }
  </Observer>
})
