import { makeAutoObservable } from "mobx";
import { SerializedTerm, ISerializableEntity, TermtanglesSerialized } from "../interfaces/SerializableInterfaces"

export class TermModel implements ISerializableEntity {
    year: number;
    name: string;

    constructor(
        year: number,
        name: string,
    ) {
        makeAutoObservable(this);
        this.checkTermConstraints(year, name);
        
        this.year = year;
        this.name = name;
    }

    static termFromSerializedData(serializedTerm: SerializedTerm) {
        return new TermModel(serializedTerm.year, serializedTerm.name);
    }

    private checkTermConstraints(year?: number, name?: string) {
        if (!year) throw Error(`ERROR: ${year} is not a valid term year.`);
        if (!name) throw Error(`ERROR: ${name} is not a valid term name.`);

        // Do other term checks here
    }

    // getShortName() {
    //     if (this.tryGetTermNumber()) {
    //         return `${this.year % 100}T${this.tryGetTermNumber()}`
    //     }
    //     return `${this.year % 100} ${this.name}`
    // }

    get getMappingName() {
        return `${this.year}_${this.name}`
    }

    update(year: number, name: string) {
        this.year = year
        this.name = name
    }

    updateFromMappingName(mappingName: string) {
        let year = parseInt(mappingName.split('_')[0])
        let name = mappingName.split('_')[1]
        this.update(year, name)
    }

    get getFullName() {
        return `${this.year} ${this.name}`
    }

    get getTermName() {
        return `${this.name}`
    }

    static fromMappingName(mappingName: string) {
        let year = parseInt(mappingName.split('_')[0])
        let term_name = mappingName.split('_')[1]

        return new TermModel(year, term_name)
    }

    private tryGetTermNumber(): number | undefined {
        switch (this.name) {
            case "Summer Term":
                return 0
            case "Term 1":
                return 1
            case "Term 2":
                return 2
            case "Term 3":
                return 3
            default:
                return undefined
        }
    }

    public equals(obj: TermModel | null): boolean {
        if (!obj) {
            return false
        }
        return this.year === obj.year && this.name === obj.name
    }

    public isBefore(term: TermModel) {
        return this.year < term.year
            || (term.year === this.year && ( (this.tryGetTermNumber() ?? 99) < (term.tryGetTermNumber() ?? 99)) );
    }

    serialize(): SerializedTerm {
        return {
            year: this.year,
            name: this.name,
        };
    }

    deserialize(serializedTerm: SerializedTerm): Promise<void> {
        throw Error(`ERROR: use static method termFromSerializedData to construct the term from serialized data ${serializedTerm}.`);
    }
}
