import { makeAutoObservable } from 'mobx';
import { ICurriculumContainer, IProgram, IProgressionEntity, ProgressionStatus } from '../interfaces/ModelInterfaces';
import { SerializedProgram, ISerializableEntity } from "../interfaces/SerializableInterfaces"

export class ProgramModel implements IProgram, IProgressionEntity, ISerializableEntity {
  public readonly id: string
  public progressionStatus: ProgressionStatus = ProgressionStatus.UNPLANNED

  constructor(
    public readonly code: string,
    public readonly title: string,
    public readonly academicItemType: string,
    public readonly additionalInfo: string,
    public readonly creditPoints: number,
    public readonly structureSummary: string,
    public readonly curriculumStructure: Array<ICurriculumContainer>,
    public readonly description: string,
    public readonly facultyName: string,
    public readonly implementationYear: number,
    public readonly intakePeriod: Array<string>,
    public readonly programConstraints: Array<any>,
    public readonly schoolName: string,
    public readonly studyLevel: string,
    public readonly urlMap: string,
  ) {
    this.id = code
    makeAutoObservable(this)
  }

  setPlanned() {
    this.progressionStatus = ProgressionStatus.PLANNED
  }

  setUnplanned() {
    this.progressionStatus = ProgressionStatus.UNPLANNED
  }

  serialize(): SerializedProgram {
    return {
      id: this.code,
    };
  }

  async deserialize(program: SerializedProgram): Promise<void> {
    if (program.id !== this.id) {
      console.error(`ERROR: This program is not ${program.id} (is: ${this.id})`);
      return;
    }
    
    this.setPlanned();
  }

  toString() {
    return `[PROGRAM_${this.id}] ${this.code} - ${this.title}`;
  }
}

