import { ErrorCode } from "../../interfaces/ModelInterfaces"
import { CourseState } from "../../states/CourseState"
import { CourseModel, CourseStatus } from "../CourseModel"

export const getCourseValidations = (courseState: CourseState): ((course: CourseModel) => void)[] => {
  const validations = [
    (course: CourseModel) => {
      // console.log(`Running validation COURSE_NOT_OFFERED_IN_SELECTED_TERM on course ${course.code}`)
      if (course.allocatedTerm && !course.termOffering.includes(course.allocatedTerm.getTermName)) {
        course.addError({
          code: ErrorCode.COURSE_NOT_OFFERED_IN_SELECTED_TERM,
          message: `This course is not offered in ${course.allocatedTerm.getTermName}`,
          missing: ""
        })
      }
    },
    (course: CourseModel) => {
      // console.log(`Running validation PREREQUISITE_REQUIREMENT_NOT_MET on course ${course.code}`)
      if (course.allocatedTerm) {
        let missing = courseState.getMissingPrerequisitesBeforeTerm(course.prerequisites, course.allocatedTerm)
        if (missing.length > 0) {
          course.addError({
            code: ErrorCode.PREREQUISITE_REQUIREMENT_NOT_MET,
            message: `The prerequisites for this course are not satisfied\n${course.enrolmentRules.join(";")}`,
            missing: `Missing: ${missing}`
          })
        }
      }
    },
    (course: CourseModel) => {
      // console.log(`Running validation PREREQUISITES_NOT_PLANNED on course ${course.code}`)
      if (course.courseStatus !== CourseStatus.ALLOCATED) {
        let missing = courseState.getMissingPrerequisitesPlanned(course.prerequisites)
        if (missing.length > 0) {
          course.addError({
            code: ErrorCode.PREREQUISITES_NOT_PLANNED,
            message: `A valid combination of prerequisites for this course have not been planned\n${course.enrolmentRules.join(";")}`,
            missing: `Missing: ${missing}`
          })
        }
      }
    },
    (course: CourseModel) => {
      // console.log(`Running validation EXCLUSION_COURSE_CONFLICT on course ${course.code}`)
      let conflictingExclusionCourses: string[] = []
      course.exclusion.forEach((exclusionCourseCode) => {
        let exclusionCourse = courseState.getCourseById(exclusionCourseCode)
        if (exclusionCourse && exclusionCourse.courseStatus !== CourseStatus.UNPLANNED) {
          conflictingExclusionCourses.push(exclusionCourseCode)
        }
      })
      if (conflictingExclusionCourses.length > 0) {
        course.addError({
          code: ErrorCode.EXCLUSION_COURSE_CONFLICT,
          message: `The following exclusion courses conflict with this course ${conflictingExclusionCourses.join(", ")}`,
          missing: ""
        })
      }
    }
  ]
  return validations
}
