import { Observer, observer } from "mobx-react"
import { useState } from "react"
import { Accordion, Form, Menu } from "semantic-ui-react"
import { FilterModel } from "../../../models/Filters/FilterModel"

export interface IFilterSectionProps<T> {
  searchResults: T[]
  filter: FilterModel<T>
}

export const FilterSection = observer(<T,>(props: IFilterSectionProps<T>) => {
  const [expandedFilters, setExpandedFilters] = useState(new Set<string>())

  return <Observer>
    {
      () => {
        let filterState = props.filter.getFilterState(props.searchResults)
        return (
            <Menu.Item key={props.filter.name}>
              <Accordion.Title
                active={expandedFilters.has(props.filter.name)}
                content={props.filter.name}
                onClick={() => {
                  let newExpandedFilters = new Set(expandedFilters)
                  if (expandedFilters.has(props.filter.name)) {
                    newExpandedFilters.delete(props.filter.name)
                  } else {
                    newExpandedFilters.add(props.filter.name)
                  }
                  setExpandedFilters(newExpandedFilters)
                }}
              />
              <Accordion.Content
                active={expandedFilters.has(props.filter.name)}
                content={
                  <Form>
                    <Form.Group grouped>
                      {
                        filterState.map(filterElementState => {
                          return <Form.Checkbox
                            key={filterElementState.filterElementName}
                            label={`${filterElementState.filterElementName}`}
                            name={props.filter.name}
                            type="checkbox"
                            value={filterElementState.filterElementName}
                            checked={props.filter.isSelected(filterElementState.filterElementName)}
                            onClick={() => {
                              props.filter.toggleValue(filterElementState.filterElementName)
                            }}
                          />
                        })
                      }
                    </Form.Group>
                  </Form>
                }
              />
            </Menu.Item>
        )
      }
    }
  </Observer>
})
