import { makeAutoObservable } from "mobx";
export type FilterValue = string

export interface IFilterElementState {
  filterElementName: string
  numMatchingRecords: number
}

export class FilterModel<T> {
  private _possibleValues: Set<FilterValue> = new Set();
  private _selectedValues: Set<FilterValue> = new Set();
  constructor(
    public readonly name: string,
    private readonly fnGetTargetValue: (searchResult: T) => FilterValue[],
  ) {
    makeAutoObservable(this)
  }

  private filterResultMapping = new Map<string, number>();
  public initializeFilter = (allSearchData: T[]) =>{
    this._possibleValues = new Set(allSearchData
      .map(result => this.fnGetTargetValue(result))
      .flat()
      .filter(value => value !== "")
    )
  }

  public applyFilter = (searchResults: T[]): T[] => {
    let results = searchResults
    if (this._selectedValues.size > 0) {
      results = searchResults
        .filter(result => [...this._selectedValues.values()]
          .filter(filterValue => this.fnGetTargetValue(result).includes(filterValue))
          .length > 0
        )
    }
    return results
  }

  public getFilterState(searchResults: T[]): IFilterElementState[] {
    const isNumerical = [...this._possibleValues.values()].filter(value => isNaN(Number(value))).length === 0
    return [...this._possibleValues.values()]
      .sort((a, b) => {
        if (isNumerical) {
          return Number(a) - Number(b)
        } else {
          return a > b ? 1 : -1
        }
      })
      .map(possibleFilterValue => {
        return {
          filterElementName: possibleFilterValue.toString(),
          numMatchingRecords: searchResults.filter(result => this.fnGetTargetValue(result).includes(possibleFilterValue)).length,
        }
      })
  }

  public isSelected(value: FilterValue) {
    return this._selectedValues.has(value)
  }

  public toggleValue(value: FilterValue) {
    if (!this._possibleValues.has(value)) {
      console.log(`invalid value toggled: ${value}`)
      return;
    }
    if (this._selectedValues.has(value)) {
      this._selectedValues.delete(value)
    } else {
      this._selectedValues.add(value)
    }
  }

  public resetValue(value: FilterValue) {
    if (!this._possibleValues.has(value)) {
      console.log(`invalid value toggled: ${value}`)
      return;
    }
    if (this._selectedValues.has(value)) {
      this._selectedValues.delete(value)
    }
  }

}