import { Observer, observer } from "mobx-react";
import { ApplicationContext } from "../../../contexts/ApplicationContext";
import { CurriculumElement } from "../../../models/CurriculumModel";
import React, { useContext } from "react";
import { CourseResultView } from "../../SearchPanel/Results/CourseResultView";
import { SpecialisationResultView } from "../../SearchPanel/Results/SpecialisationResultView";
import { ResultCollection } from "../../SearchPanel/SearchPanelStyles";

export interface ICurriculumElementViewProps {
  entityId: string,
  curriculumElements: CurriculumElement[]
}

export const CurriculumElementsView = observer((props: ICurriculumElementViewProps) => {
  const applicationContext = useContext(ApplicationContext);
  return <Observer>
    {
      () => {
        return props.curriculumElements.length > 0
          ? <ResultCollection divided>
            {
              props.curriculumElements
                .sort((a, b) => Number(a.order) - Number(b.order))
                .map(element => {
                  if (element.isCourse()) {
                    let course = applicationContext.courseState.getCourseById(element.itemCode)
                    return course ? <CourseResultView course={course} /> : <></>
                  } else if (element.isSpecialisation()) {
                    let specialisation = applicationContext.progressionState.getSpecialisationById(element.itemCode)
                    return specialisation ? <SpecialisationResultView specialisation={specialisation} /> : <></>
                  }
                  return <p>{element.description}</p>
                })
            }
          </ResultCollection>
          : <></>
      }
    }
  </Observer>
});
