import { Observer, observer } from "mobx-react"
import { ProgramModel } from "../../../models/ProgramModel"
import { ProgramResultView } from "./ProgramResultView"
import { ResultCollection } from "../SearchPanelStyles"

export interface IResultCollectionViewProps<T> {
  results: T[]
}

export const ProgramResultCollectionView = observer((props: IResultCollectionViewProps<ProgramModel>) => {
  return <Observer>
    {
      () => {
        return (
          <ResultCollection divided>
            {
              props.results.map(program => {
                return (
                  <ProgramResultView program={program} />
                )
              })
            }
          </ResultCollection>
        )
      }
    }
  </Observer>
})
