import { ConditionalTree, isAndConditionalTree, isOrConditionalTree } from "../interfaces/ModelInterfaces";


export const conditionalTreeTraverser = (
  treeNode: ConditionalTree,
  fnIsLeafValid: (treeNode: ConditionalTree) => boolean,
): string[] => {
  let invalidations: string[] = [];

  if (isOrConditionalTree(treeNode)) {
    for (let childNode of treeNode.or) {
      let result = conditionalTreeTraverser(childNode, fnIsLeafValid)
      if (result.length === 0) {
        invalidations = []
        break
      }
      invalidations.push(...result)
    }
  } else if (isAndConditionalTree(treeNode)) {
    treeNode.and.forEach(childNode => {
      invalidations.push(...conditionalTreeTraverser(childNode, fnIsLeafValid))
    })
  } else if (!fnIsLeafValid(treeNode)) {
    invalidations.push(treeNode)
  }
  // console.log(treeNode)
  // console.log(invalidations)
  return invalidations
}
