import "fomantic-ui-css/semantic.min.css";
import { Observer } from 'mobx-react-lite';
import React, { useContext } from 'react';
import SplitPane from 'react-split-pane';
import 'semantic-ui-react';
import { Container } from 'semantic-ui-react';
import './App.css';
import LocalImportExport from "./containers/LocalImportExport/LocalImportExport"
import { LocalImportExportViewModel } from './containers/LocalImportExport/LocalImportExportViewModel';
import { MenuBar } from './containers/MenuBar/MenuBar';
import Planner from './containers/Planner/Planner';
import { PlannerViewModel } from './containers/Planner/PlannerViewModel';
import ProgressionPlanner from './containers/ProgressionPlanner/ProgressionPlanner';
import { ProgressionPlannerViewModel } from './containers/ProgressionPlanner/ProgressionPlannerViewModel';
import SearchPanel from './containers/SearchPanel/SearchPanel';
import { SearchPanelViewModel } from './containers/SearchPanel/SearchPanelViewModel';
import { ApplicationContext } from './contexts/ApplicationContext';
import AutoPlanner from './containers/AutoPlan/AutoPlannerView';
import { AutoPlannerViewModel } from "./containers/AutoPlan/AutoPlannerModel";

const App = () => {
  const applicationContext = useContext(ApplicationContext)

  const searchPanelViewModel = new SearchPanelViewModel(applicationContext.searchDataState);
  const plannerViewModel = new PlannerViewModel(applicationContext.courseState);
  const progressionPlannerViewModel = new ProgressionPlannerViewModel(applicationContext.progressionState, applicationContext.courseState);
  const localImportExportModel = new LocalImportExportViewModel(applicationContext.userState, applicationContext.progressionState, applicationContext.courseState);
  const autoPlannerViewModel = new AutoPlannerViewModel(applicationContext.courseState);

  return (
    <ApplicationContext.Provider value={applicationContext}>
      <div className="ui maincontainer">
        <MenuBar userState={applicationContext.userState} />
        <SplitPane
          className="split-pane-flex"
          split="vertical"
          defaultSize={"min(50vw, 1000px)"}
          primary="second"
        >
          <Container style={{ padding: 10 }} fluid>
            <SearchPanel searchPanelViewModel={searchPanelViewModel} />
            <Observer>
              {
                () => {
                  return (
                    !searchPanelViewModel.shouldShowPanel
                      ? <ProgressionPlanner progressionPlannerViewModel={progressionPlannerViewModel} />
                      : <></>
                  )
                }
              }
            </Observer>
          </Container>
          <Container style={{ padding: 10 }} fluid>
            <LocalImportExport localImportExportViewModel={localImportExportModel} />
            <AutoPlanner autoPlannerViewModel={autoPlannerViewModel} />
            <Planner plannerViewModel={plannerViewModel} />
          </Container>
        </SplitPane>
      </div>
    </ApplicationContext.Provider>
  )
}

export default App;
