import { Observer, observer } from "mobx-react"
import { SpecialisationModel } from "../../../models/SpecialisationModel"
import { SpecialisationResultView } from "./SpecialisationResultView"
import { ResultCollection } from "../SearchPanelStyles"

export interface IResultCollectionViewProps<T> {
  results: T[]
}

export const SpecialisationResultCollectionView = observer((props: IResultCollectionViewProps<SpecialisationModel>) => {
  return <Observer>
    {
      () => {
        return (
          <ResultCollection divided>
            {
              props.results.map(specialisation => {
                return (
                  <SpecialisationResultView specialisation={specialisation} />
                )
              })
            }
          </ResultCollection>
        )
      }
    }
  </Observer>
})
