import { makeAutoObservable, when } from "mobx";
import { StudyLevel } from "../interfaces/ModelInterfaces";
import { SerializedUserState, ISerializableEntity, TermtanglesSerialized } from "../interfaces/SerializableInterfaces"

const stringToStudyLevelMap = ((str: string) => {
  switch(str) {
    case "undergraduate": return StudyLevel.UNDERGRADUATE;
    case "postgraduate": return StudyLevel.POSTGRADUATE;
    case "research": return StudyLevel.RESEARCH;
    default: return undefined;
  }
});

export class UserState implements ISerializableEntity {
  constructor() {
    makeAutoObservable(this)
  }

  private _studyLevel: StudyLevel = StudyLevel.UNDERGRADUATE
  private _isUpdated: boolean = true;
  private _studyLevelUpdateActionCallbacks: (() => Promise<void>)[] = [];

  get studyLevel() {
    return this._studyLevel
  }

  async setStudyLevel (studyLevel: StudyLevel) {
    if (this._studyLevel !== studyLevel) {
      this._studyLevel = studyLevel;
      const studyLevelPromises: Promise<void>[] = this._studyLevelUpdateActionCallbacks.map(callback => callback());
      return Promise.all(studyLevelPromises);
    }
  }

  set isUpdated(isUpdated: boolean) {
    this._isUpdated = isUpdated;
  }
  
  get isUpdated(): boolean {
    return this._isUpdated;
  }

  registerStudyLevelUpdateActionCallback(updateAction: () => Promise<void>) {
    this._studyLevelUpdateActionCallbacks.push(updateAction);
  }

  serialize(): SerializedUserState {
    return {studyLevel: this._studyLevel};
  }

  async deserialize(json: TermtanglesSerialized): Promise<void> {
    if (!json.userState) {
      throw "ERROR: User state not included.";
    }

    const serializedUserState = json.userState;
    if (!serializedUserState.studyLevel) {
      throw `ERROR: User state must include a studyLevel parameter.`;
    }

    const level = stringToStudyLevelMap(serializedUserState.studyLevel);
    if (!level) {
      throw `ERROR: ${serializedUserState.studyLevel} is not a valid study level.`
    }

    await this.setStudyLevel(level);
  }
}
