import { CourseModel } from "../CourseModel"
import { ProgramModel } from "../ProgramModel"
import { SpecialisationModel } from "../SpecialisationModel"
import { FilterModel } from "./FilterModel"

export const getCourseFilters = (allSearchData: CourseModel[]) => {
  const filters: FilterModel<CourseModel>[] = [
    new FilterModel<CourseModel>("Offering Periods", (course) => course.termOffering),
    new FilterModel<CourseModel>("Faculty", (course) => [course.facultyName]),
    new FilterModel<CourseModel>("School", (course) => [course.schoolName]),
    new FilterModel<CourseModel>("Credit Points", (course) => [course.creditPoints.toString()]),
  ]
  filters.forEach(filter => filter.initializeFilter(allSearchData))
  return filters
}

