import { Observer, observer } from "mobx-react"
import { useState } from "react"
import { Button, Icon, Item } from "semantic-ui-react"
import { CourseModel, CourseStatus } from "../../../models/CourseModel"
import { ResultItem, ResultItemContent, ResultItemExtra, ResultItemHeader } from "../SearchPanelStyles"

export interface IResultViewProps<T> {
  course: T
}

export const CourseResultView = observer((props: IResultViewProps<CourseModel>) => {
  const [showOptions, setShowOptions] = useState(false)

  const openInNewTab = (url) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
  }

  return <Observer>
    {
      () => {
        return (
          <ResultItem
            onMouseOver={() => setShowOptions(true)}
            onMouseLeave={() => setShowOptions(false)}
          >
            <ResultItemContent>
              <ResultItemHeader>{props.course.code}</ResultItemHeader>
              <Item.Description>{props.course.title}</Item.Description>
            </ResultItemContent>
            {
              !showOptions
              ?
                <ResultItemContent>
                  <ResultItemExtra>{props.course.creditPoints} UOC</ResultItemExtra>
                  <ResultItemExtra>
                    {
                      props.course.courseStatus === CourseStatus.PLANNED
                        ? <Icon color="red" className="bookmark" />
                        : props.course.courseStatus === CourseStatus.ALLOCATED
                          ? <Icon color="orange" className="calendar alternate outline" />
                          : props.course.courseStatus === CourseStatus.COMPLETED
                            ? <Icon color="green" className="check" />
                            : ""
                    }

                  </ResultItemExtra>
                </ResultItemContent>
                :
                <ResultItemContent>
                  <ResultItemExtra>
                    <Button.Group>
                      <Button
                        basic = {props.course.courseStatus !== CourseStatus.PLANNED && props.course.courseStatus !== CourseStatus.ALLOCATED}
                        icon={props.course.courseStatus === CourseStatus.ALLOCATED ? 'calendar alternate outline' : 'bookmark'}
                        content={props.course.courseStatus === CourseStatus.ALLOCATED ? 'Scheduled' : 'Interested'}
                        color={props.course.courseStatus === CourseStatus.ALLOCATED ? 'orange' : 'blue'}
                        size="medium"
                        onClick= {() => {
                          if (props.course.courseStatus === CourseStatus.PLANNED || props.course.courseStatus === CourseStatus.ALLOCATED) {
                            props.course.setUnplanned()
                          } else {
                            props.course.setPlanned()
                          }
                        }}
                      />
                      <Button
                        basic = {props.course.courseStatus !== CourseStatus.COMPLETED}
                        icon='check'
                        content='Done'
                        color="green"
                        size="medium"
                        onClick= {() => {
                          if (props.course.courseStatus === CourseStatus.COMPLETED) {
                            props.course.setUnplanned()
                          } else {
                            props.course.setCompleted()
                          }
                        }}
                      />
                      <Button
                        basic = {true}
                        icon={'book'}
                        content={'Handbook'}
                        color={'red'}
                        size="medium"
                        onClick= {() => openInNewTab(`https://www.handbook.unsw.edu.au${props.course.urlMap}`) }
                      />
                    </Button.Group>
                  </ResultItemExtra>
                </ResultItemContent>
            }
          </ResultItem>
        )
      }
    }
  </Observer>
})
