import styled from 'styled-components'
import { Observer, observer } from 'mobx-react';
import React, { useState } from 'react';
import { Button, Menu, Modal } from 'semantic-ui-react';
import { StudyLevel } from '../../interfaces/ModelInterfaces';
import { UserState } from '../../states/UserState';

export interface IMenuBarProps {
  userState: UserState
}

const MenuBarView = styled(Menu)`
  &&{
    margin: auto 0 auto 0;
  }
`

export const MenuBar = observer((props: IMenuBarProps) => {
  const [studyLevelChangeAction, setStudyLevelChangeAction] = useState<(() => void) | undefined>(undefined)
  return <Observer>
    {() => {
      return (
        <React.Fragment>
          <MenuBarView pointing>
            <Menu.Item header>Termtangles</Menu.Item>
            <Menu.Item
              name={"Undergraduate"}
              active={props.userState.studyLevel === StudyLevel.UNDERGRADUATE}
              onClick={
                () => props.userState.studyLevel !== StudyLevel.UNDERGRADUATE
                  ? setStudyLevelChangeAction(() => () => props.userState.setStudyLevel(StudyLevel.UNDERGRADUATE))
                  : {}
              }
            />
            <Menu.Item
              name={"Postgraduate"}
              active={props.userState.studyLevel === StudyLevel.POSTGRADUATE}
              onClick={
                () => props.userState.studyLevel !== StudyLevel.POSTGRADUATE
                  ? setStudyLevelChangeAction(() => () => props.userState.setStudyLevel(StudyLevel.POSTGRADUATE))
                  : {}
              }
            />
            <Menu.Item
              name={"Research"}
              active={props.userState.studyLevel === StudyLevel.RESEARCH}
              onClick={
                () => props.userState.studyLevel !== StudyLevel.RESEARCH
                  ? setStudyLevelChangeAction(() => () => props.userState.setStudyLevel(StudyLevel.RESEARCH))
                  : {}
              }
            />
          </MenuBarView>

          <Modal
            size='small'
            open={studyLevelChangeAction !== undefined}
            onClose={() => setStudyLevelChangeAction(undefined)}
          >
            <Modal.Header>Study Level Change</Modal.Header>
            <Modal.Content>
              <p>Changing study level will reset all current progress. Do you want to continue?</p>
            </Modal.Content>
            <Modal.Actions>
              <Button negative onClick={() => setStudyLevelChangeAction(undefined)}>
                No
              </Button>
              <Button positive onClick={() => {
                if (studyLevelChangeAction) {
                  studyLevelChangeAction()
                }
                setStudyLevelChangeAction(undefined)
              }}>
                Yes
              </Button>
            </Modal.Actions>
          </Modal>

        </React.Fragment>
      )
    }}
  </Observer>
})
