import { Observer, observer } from "mobx-react"
import React from "react"
import { Item } from "semantic-ui-react"
import styled from 'styled-components'
import { CourseModel } from "../../../models/CourseModel"
import { ResultCollection } from "../SearchPanelStyles"
import { CourseResultView } from "./CourseResultView"

export interface IResultCollectionViewProps<T> {
  results: T[]
}

const CourseCollection = styled(Item.Group)`
  &&& {
    padding: 15px 0px 10px 0px;
  }
`
        
export const CourseResultCollectionView = observer((props: IResultCollectionViewProps<CourseModel>) => {
  return <Observer>
    {
      () => {
        return (
          <ResultCollection divided>
            {
              props.results.map(course => {
                return (
                  <CourseResultView course={course} />
                )
              })
            }
          </ResultCollection>
        )
      }
    }
  </Observer>
})
