import { Droppable } from 'react-beautiful-dnd';
import { Label } from 'semantic-ui-react';
import styled from 'styled-components'
import { CourseModel } from '../../../models/CourseModel';
import { TermModel } from '../../../models/TermModel';
import CourseView from '../CourseView/CourseView';
import { ICurrentDraggableProps } from '../PlannerViewModel';

const Container = styled.div`
    margin: 8px;
    border: 1px solid lightgrey;
    border-radius:2px;

    display: flex;
    flex-direction: column;
    flex-grow: 1;
    ${props => !props.horizontal? "max-width: 350px;" : ""}
`

const Title = styled.h3`
    padding: 6px;
    margin: 0 0;
    display: flex;
    width: 200px;
`;

const CourseList = styled.div`
    padding: 6px;
    min-height: 100px;
    transition: background-color 0.2s ease;
    background-color: ${props =>
        (props.isDraggingOver
            ? props.isValidDroppable ? '#8debb7' : '#ff99b0'
            : props.isPossibleTermOfCurrentDraggable
                ? '#cafae0'
                :(props.courseID === "completed")
                    ? '#edf2f4'
                    : (props.courseID === "planned")
                        ? '#edf2f4'
                        : '#edf2f4'
        )
    };
    flex-grow: 1;
    display: flex;
    flex-direction: ${props => props.horizontal ? "row" : "column"};
    flex-flow: row ${props => props.horizontal ? "wrap" : "nowrap"};
    align-content: flex-start;
`;



const TermHeader = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
`

const UocLabel = styled(Label)`
    && {
        margin: auto 0 auto 0;
        height: fit-content;
        vertical-align: middle;
        white-space: nowrap;
    }
`

interface ITermViewProps {
    id: string
    title: string
    courses: CourseModel[]
    term: TermModel | null
    currentDraggableProps: ICurrentDraggableProps
    horizontal?: boolean
}

const CourseGroupView = ({ id, title, courses, term, currentDraggableProps, horizontal}: ITermViewProps) => {
    const isPossibleTerm = (term: TermModel | null): boolean => {
        return currentDraggableProps.possibleTerms.filter(_term => _term.equals(term)).length > 0
    }
    const totalUoc = courses.map(course => course.creditPoints).reduce((a, b) => a + b, 0)
    return (
        <Container horizontal={horizontal}>
            <TermHeader>
                <Title>{title}</Title>
                {
                    term
                    ? <UocLabel
                        color={(totalUoc === 18 || courses.length === 3)
                            ? "green"
                            : totalUoc < 18
                                ? "orange"
                                : "yellow"
                        }
                        content={`${totalUoc} UOC`}
                        size={"small"}
                    />
                    : <></>
                }
            </TermHeader>
            <Droppable
                droppableId={id}
                direction={horizontal ? 'horizontal' : 'vertical'}
            >
                {
                    (provided, snapshot) => (
                        <CourseList
                            horizontal
                            {...provided.droppableProps}
                            ref={provided.innerRef}
                            isDraggingOver={snapshot.isDraggingOver}
                            courseID={provided.droppableProps['data-rbd-droppable-id']}
                            isPossibleTermOfCurrentDraggable={isPossibleTerm(term)}
                            isValidDroppable={term === null || isPossibleTerm(term)}
                        >
                            {
                                courses
                                    ? courses.map((course, index) =>
                                    <CourseView
                                        horizontal={horizontal}
                                        key={course.code}
                                        course={course}
                                        droppableIndex={index}
                                        prerequisitesOfCurrentDraggable={currentDraggableProps.prerequisiteCourseIds}
                                    />)
                                    : undefined
                            }
                            {provided.placeholder}
                        </CourseList>
                    )
                }
            </Droppable>
        </Container>
    )
}
export default CourseGroupView
