import { action, makeAutoObservable, reaction, transaction, when } from "mobx";
import { conditionalTreeTraverser } from '../helpers/ConditionalTreeTraverser';
import { ConditionalTree, StudyLevel } from "../interfaces/ModelInterfaces";
import { CourseModel, CourseStatus } from "../models/CourseModel";
import { SerializedCourseState, ISerializableEntity, TermtanglesSerialized } from "../interfaces/SerializableInterfaces"
import { TermModel } from "../models/TermModel";
import { getCourseValidations } from "../models/Validations/Validations";
import { UserState } from "./UserState";
import axios from "axios"

export class CourseState implements ISerializableEntity {
  constructor(
    private _userState: UserState
  ) {
    makeAutoObservable(this)
    reaction(
      () => this._userState.studyLevel,
      () => {
        this._isUpdated = false;
        this._updateStudyLevel()
          // .then(() => {
          //   // this.getCourseById("COMP1511")?.setCompleted()
    
          //   this.getCourseById("COMP1521")?.setPlanned()
    
          //   this.getCourseById("COMP3231")?.setPlanned()
          //   let COMP9444 = this.getCourseById("COMP9444")
          //   if (COMP9444) {
          //     this.assignCourseToTerm(COMP9444, new TermModel(2021, "Term 3"))
          //   }
          //   let MATH1081 = this.getCourseById("MATH1081")
          //   if (MATH1081) {
          //     this.assignCourseToTerm(MATH1081, new TermModel(2021, "Term 2"))
          //   }
          //   let COMP2041 = this.getCourseById("COMP2041")
          //   if (COMP2041) {
          //     this.assignCourseToTerm(COMP2041, new TermModel(2021, "Term 2"))
          //   }
          //   let COMP9417 = this.getCourseById("COMP9417")
          //   if (COMP9417) {
          //     this.assignCourseToTerm(COMP9417, new TermModel(2022, "Term 3"))
          //   }
          //   let MATH1231 = this.getCourseById("MATH1231")
          //   if (MATH1231) {
          //     this.assignCourseToTerm(MATH1231, new TermModel(2021, "Term 3"))
          //   }
          // })
      },
      { fireImmediately: true }
    );
    _userState.registerStudyLevelUpdateActionCallback(() => Promise.resolve(this._updateStudyLevel()));
  }

  private validations = getCourseValidations(this)

  private coursesMapById = new Map<string, CourseModel>();

  private coursesMapByActiveStatus = new Map<CourseStatus, Map<string, CourseModel>>();

  private _isUpdated: boolean = false;

  get isUpdated(): boolean {
    return this._isUpdated;
  }

  get allCourses() {
    return [...this.coursesMapById.values()]
  }

  private _resetCourseMap() {
    this.coursesMapById = new Map<string, CourseModel>();
    this.coursesMapByActiveStatus = new Map<CourseStatus, Map<string, CourseModel>>();
    this.coursesMapByActiveStatus.set(CourseStatus.UNPLANNED, new Map<string, CourseModel>());
    this.coursesMapByActiveStatus.set(CourseStatus.PLANNED, new Map<string, CourseModel>());
    this.coursesMapByActiveStatus.set(CourseStatus.ALLOCATED, new Map<string, CourseModel>());
    this.coursesMapByActiveStatus.set(CourseStatus.COMPLETED, new Map<string, CourseModel>());
  }

  private async _loadAllCourses(studyLevel: StudyLevel) {
    return axios.get(`https://raw.githubusercontent.com/termtangles/termtangles_data/main/publish/${studyLevel.toString()}/all_courses.json`)
      .then(response => {
        transaction(() => {
          this._resetCourseMap()
          response.data.forEach(courseObj => {
            let course = new CourseModel(
              courseObj.code,
              courseObj.contentTypeLabel,
              courseObj.corequisites,
              courseObj.creditPoints,
              courseObj.description,
              courseObj.effectiveDate,
              courseObj.enrolmentRules,
              courseObj.exclusion,
              courseObj.facultyName,
              courseObj.implementationYear,
              courseObj.keywords,
              courseObj.prerequisites,
              courseObj.schoolName,
              courseObj.studyLevel,
              courseObj.termOffering,
              courseObj.title,
              courseObj.urlMap,
            )

            this.addCourse(course)
          })
        })
    });
  }

  private async _updateStudyLevel() {
    await this._loadAllCourses(this._userState.studyLevel);
    this._isUpdated = true;
  }

  get termCourseMap(): Map<string, CourseModel[]> {
    // console.log('running mapping')
    let mapping = new Map<string, CourseModel[]>();
    this.coursesMapByActiveStatus.get(CourseStatus.ALLOCATED)?.forEach(course => {
      if (!course.allocatedTerm) {
        return;
      }
      let courseList = mapping.get(course.allocatedTerm.getMappingName)
      if (!courseList) {
        mapping.set(course.allocatedTerm.getMappingName, [course])
      } else {
        courseList.push(course)
      }
    })
    return mapping
  }

  get completedCourses(): CourseModel[] {
    return [...this.coursesMapByActiveStatus.get(CourseStatus.COMPLETED)?.values() ?? []]
  }

  get plannedCourses(): CourseModel[] {
    return [...this.coursesMapByActiveStatus.get(CourseStatus.PLANNED)?.values() ?? []]
  }

  get allocatedCourses(): CourseModel[] {
    return [...this.coursesMapByActiveStatus.get(CourseStatus.ALLOCATED)?.values() ?? []]
  }

  get activeCourses(): CourseModel[] {
    let allCourses: CourseModel[] = [];
    allCourses = allCourses.concat(this.completedCourses, this.plannedCourses, this.allocatedCourses);
    return allCourses;
  }

  get getMaxAllocatedYear(): number {
    const uniqueTerms = [...this.termCourseMap.keys()]
    let maxYear = new Date().getFullYear()
    if (uniqueTerms.length > 0) {
      maxYear = TermModel.fromMappingName(uniqueTerms.sort().slice(-1)[0]).year
    }
    return maxYear
  }

  getCourseById(id: string): CourseModel | undefined {
    return this.coursesMapById.get(id)
  }

  addCourse(course: CourseModel) {
    this.coursesMapById.set(course.id, course);
    reaction(() => course.courseStatus, () => {
      for (let courseStatusEnum in CourseStatus) {
        if (!isNaN(Number(courseStatusEnum))) continue
        const courseStatus: CourseStatus = CourseStatus[courseStatusEnum as keyof typeof CourseStatus]
        if (courseStatus !== course.courseStatus) {
          this.coursesMapByActiveStatus.get(courseStatus)?.delete(course.id)
        } else {
          this.coursesMapByActiveStatus.get(courseStatus)?.set(course.id, course)
        }
      }
    }, {
      fireImmediately: true,
    })

    reaction(() => course.courseStatus, () => {
      this.runValidations();
    }, {
      fireImmediately: true,
    })

    reaction(() => course.allocatedTerm, () => {
      this.runValidations();
    }, {
      fireImmediately: true,
    })
  }

  runValidations() {
    transaction(() => {
      this.coursesMapByActiveStatus.get(CourseStatus.PLANNED)?.forEach(_course => {
        _course.clearErrors()
        this.validations.forEach(fnValidate => {
          fnValidate(_course)
        })
      })
      this.coursesMapByActiveStatus.get(CourseStatus.ALLOCATED)?.forEach(_course => {
        _course.clearErrors()
        this.validations.forEach(fnValidate => {
          fnValidate(_course)
        })
      })
      this.coursesMapByActiveStatus.get(CourseStatus.COMPLETED)?.forEach(_course => {
        _course.clearErrors()
      })
    })
  }

  private resetHard() {
    [...this.coursesMapById.values()].forEach((course) => {
      course.setUnplanned();
    });
  }

  getFlattenedPrerequisiteList(prerequisiteTree: ConditionalTree) {
    return conditionalTreeTraverser(prerequisiteTree, (treeNode: ConditionalTree) => {
      return false
    })
  }

  getMissingPrerequisitesBeforeTerm(prerequisiteTree: ConditionalTree, term: TermModel) {
    return conditionalTreeTraverser(prerequisiteTree, (treeNode: ConditionalTree) => {
      if (typeof (treeNode) === 'string') {
        const courseCode: string = treeNode;
        const course = this.getCourseById(courseCode)

        return course
          ? ((course.courseStatus === CourseStatus.ALLOCATED && course.allocatedTerm?.isBefore(term))
            || course.courseStatus === CourseStatus.COMPLETED)
          : false
      }
      return true
    })
  }

  getMissingPrerequisitesPlanned(prerequisiteTree: ConditionalTree) {
    return conditionalTreeTraverser(prerequisiteTree, (treeNode: ConditionalTree) => {
      if (typeof (treeNode) === 'string') {
        const courseCode: string = treeNode;
        const course = this.getCourseById(courseCode)
        return course
          ? course.courseStatus !== CourseStatus.UNPLANNED
          : false
      }
      return true
    })
  }

  courseIsOffered(term: TermModel, termOffering: string[]): boolean {
    for (let i = 0; i < termOffering.length; i++) {
      if (term.getTermName === termOffering[i]) {
        return true;
      }
    }

    return false;
  }

  getMissingRequirementsWhenAssigningCourseToTerm(course: CourseModel, term: TermModel): string[] {
    let resp: string[] = this.getMissingPrerequisitesBeforeTerm(course.prerequisites, term)

    // term = current term input, termOffering = List of avaliable terms
    if (!this.courseIsOffered(term, course.termOffering)) {
      resp.push(`Course is not offered in ${term.getFullName}`)
    }

    return resp
  }

  assignCourseToTerm(course: CourseModel, term: TermModel): boolean {
    const invalidations = this.getMissingRequirementsWhenAssigningCourseToTerm(course, term)
    if (invalidations.length === 0) {
      course.setAllocated(term)
      return true
    }

    return false
  }

  serialize(): SerializedCourseState {
    return {
      courses: this.activeCourses.map((courseModel) => {
        return courseModel.serialize();
      })
    }
  }

  async deserialize(json: TermtanglesSerialized): Promise<void> {
    if (!json.courseState) {
      return;
    }

    const serializedCourseState = json.courseState;
    if (!serializedCourseState.courses) {
      return;
    }

    await when(() => { return this._isUpdated; })

    this.resetHard();

    serializedCourseState.courses.forEach((serializedCourse) => {
      if (!serializedCourse.id) return;

      const courseModel = this.getCourseById(serializedCourse.id);
      if (courseModel) {
        courseModel.deserialize(serializedCourse);
      } else {
        console.error(`ERROR: ${serializedCourse.id} is not a valid course.`);
      }
    });
  }
}
